import React from 'react';

class AudioPlayer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date()
    };
  }

  
  render() {
    let imgSource = "../../../static/images/pause.svg"
   
    return (
      <div>
          <audio src="../static/audio/linkinpark.mp3" controls autoPlay hidden/>
          <button onClick={this.onPause}><img src={imgSource}/></button>
      </div>
    );
  }
}

export default AudioPlayer;